<template>
	<div class="RFPick_select">
		<header><i class="iconfont icon-xiangzuo1" @click="toReceipt"></i><span>选择任务组</span></header>
		<div class="container">
			<table cellspacing="0">
				<thead>
					<tr>
						<td>选择</td>
						<td  @click="SortArr('RFPick_SelectOrderData','TaskGroup','SortCondition1',$event)">任务组号</td>
						<td  @click="SortArr('RFPick_SelectOrderData','Qty','SortCondition2',$event)">拣货数量</td>
						<td  @click="SortArr('RFPick_SelectOrderData','TaskCnt','SortCondition3',$event)">任务数</td>
						<td  @click="SortArr('RFPick_SelectOrderData','LocCnt','SortCondition4',$event)">库位数</td>
						<td  @click="SortArr('RFPick_SelectOrderData','SkuCnt','SortCondition5',$event)">商品品类数</td>
						<td  @click="SortArr('RFPick_SelectOrderData','AddDate','SortCondition6',$event)">任务下发时间</td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in RFPick_SelectOrderData'  :id="index"  :key='index'  @click="selectData(index)">
						<td class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></td>
						<td>{{dataItem.TaskGroup}}</td>
						<td>{{dataItem.Qty}}</td>
						<td>{{dataItem.TaskCnt}}</td>
						<td>{{dataItem.LocCnt}}</td>
						<td>{{dataItem.SkuCnt}}</td>
						<td>{{dataItem.AddDate}}</td>
					</tr>
				</tbody>
			</table>
			<div class="kong"></div>
			<p class="positionFixed" ><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"RFPick_select",
		data(){
			return{

				RFPick_SelectOrderData:[],//列表数据
				detail_item:0,//默认为第一个
				SortCondition1:false,
				SortCondition2:false,
				SortCondition3:false,
				SortCondition4:false,
				SortCondition5:false,
				SortCondition6:false,
			}
		},
		mounted(){
			$("thead").find("td").eq(0).siblings().append('<i class="iconfont"></i>')
		},
		created(){
			console.log(this.$store.state.RFPick_SelectOrderData)

			if(this.$store.state.RFPick_SelectOrderData.length){

				var RFPick_SelectOrderData = this.$store.state.RFPick_SelectOrderData;

				this.RFPick_SelectOrderData = RFPick_SelectOrderData;

			}
		},
		methods:{
			// 返回上一页 录入单号 页面
			toReceipt:function(){

				var that = this;

				this.$router.push({
					name:"RFPick",
					query:{
						TaskGroup:that.$route.query.TaskGroup
					}
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;

				
			},
			// 点击确认按钮 跳转到录入单号页面
			selectData_submit:function(){


				var TaskGroup = this.RFPick_SelectOrderData[this.detail_item].TaskGroup//任务组号

				this.$router.push({
					name:"RFPick",
					query:{
						TaskGroup:TaskGroup
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.RFPick_select{
		&>.container{
			width:100%;
			padding-bottom: 130/50rem;
			table{
				tr{
					td:nth-child(2),td:nth-child(3){
						min-width: 160/50rem;
					}
					td:nth-child(4),td:nth-child(5){
						min-width: 130/50rem;
					}
					td:nth-child(6){
						min-width: 200/50rem;
					}
					td:last-child{
						min-width: 400/50rem;
					}
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
		}
	}
	@media screen and (max-width: 332px) and (min-width: 251px){
		.RFPick_select{
			&>.container{
				table{
					tr{
						td:nth-child(2),td:nth-child(3){
							min-width: 200/50rem;
						}
						td:nth-child(4),td:nth-child(5){
							min-width: 170/50rem;
						}
						td:nth-child(6){
							min-width: 250/50rem;
						}
						td:last-child{
							min-width: 440/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
	@media screen and (max-width: 250px){
		.RFPick_select{
			&>.container{
				table{
					tr{
						td:nth-child(2),td:nth-child(3){
							min-width: 260/50rem;
						}
						td:nth-child(4),td:nth-child(5){
							min-width: 210/50rem;
						}
						td:nth-child(6){
							min-width: 300/50rem;
						}
						td:last-child{
							min-width: 490/50rem;
						}
					}
				}
				.iconfont{
					font-size: 50/50rem;
				}
			}
		}
	}
</style>


